import React from "react";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import ReactDatetime from "react-datetime";
import {
  Container,
  Button,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
} from "reactstrap";
import Loading from "components/Loading";
import { fixNumber, getUserFullName } from "helpers/UtilsHelper";
import { getManualInvoiceDetail } from "../../../../redux/actions/billingAction";
import "./CreateInvoice.scss";

export default function InvoiceDetail(props) {
  const dispatch = useDispatch();
  const invoiceId = props.match.params.id;

  const manualInvoiceData = useSelector((state) => state.billing.data);
  const loading = useSelector((state) => state.billing.loading);

  useEffect(() => {
    dispatch(getManualInvoiceDetail(invoiceId));
  }, []);

  const { invoiceNumber, provider, date, comment, amount } = manualInvoiceData;
  const providerName = getUserFullName(provider);
  return (
    <Container className="create-invoice">
      {loading && <Loading />}
      <Card>
        <CardHeader>
          <Row className="d-flex">
            <Col
              md={6}
              xs={12}
              className="d-flex flex-column align-items-center"
            >
              <h2>
                <b className="text-primary">EtheraPay</b>
              </h2>
              <small>Invoice: #{invoiceNumber}</small>
            </Col>
            <Col md={6} xs={12} className="mt-3 mt-md-0">
              <Row className="d-flex align-items-center justify-content-center">
                <b className="text-primary mr-3">Provider:</b>
                <div className="width-200">
                  <Input value={providerName} name="provider" readOnly />
                </div>
              </Row>
            </Col>
          </Row>
        </CardHeader>
        <CardBody>
          <Row className="tb-custom">
            <Col md={4} lg={3}>
              <Row className="header">
                <b>Date</b>
              </Row>
              <Row className="body">
                <ReactDatetime
                  className="rdt"
                  id="date"
                  value={moment(date)}
                  inputProps={{
                    placeholder: "MM/DD/YYYY",
                  }}
                  closeOnSelect={true}
                  dateFormat={true}
                  timeFormat={false}
                  // onChange={date => this.handleSelectedDate("date", date)}
                  readOnly
                />
              </Row>
            </Col>
            <Col md={6} lg={7}>
              <Row className="header">
                <b>Comment</b>
              </Row>
              <Row className="body">
                <Input
                  // type="text"
                  name="comment"
                  // onChange={this.handleChange}
                  defaultValue={comment}
                  placeholder="Type comment..."
                  readOnly
                />
              </Row>
            </Col>
            <Col md={2} lg={2}>
              <Row className="header">
                <b>Amount</b>
              </Row>
              <Row className="body">
                <InputGroup>
                  <Input
                    step="0.01"
                    name="amount"
                    defaultValue={amount}
                    placeholder="Type amount..."
                    min="0"
                    readOnly
                  />
                  <InputGroupAddon addonType="append">
                    <InputGroupText>
                      <i className="fas fa-dollar-sign" />
                    </InputGroupText>
                  </InputGroupAddon>
                </InputGroup>
              </Row>
            </Col>
          </Row>
        </CardBody>
        <CardFooter>
          <Col sm={12} className="text-center">
            <span className="font-weight-bold">
              Total Amount Due: ${fixNumber(amount)}
            </span>
          </Col>
          <Row className="mt-3">
            <Col className="text-center">
              <Button color="secondary" onClick={() => props.history.goBack()}>
                Go Back
              </Button>
            </Col>
          </Row>
        </CardFooter>
      </Card>
    </Container>
  );
}
